import { Row, Col, Input, Button } from 'element-ui'

export default {
    components: {
        'el-row': Row,
        'el-col': Col,
        'el-input': Input,
        'el-button': Button
    },
    props: ['data'],
    methods: {
        canEdit() {
            this.cannotEdit = false;
        },
        edit() {
            this.loading = true
            axios.post('api/system/power/modifyPermissionGroup', {id: this.group.id, name: this.group.name}).then(response => {
                this.$message.success(response.data.message)
            })
            this.cannotEdit = true
            this.loading = false
        },
        //
        del(id) {
            this.loading = true
            axios.post('api/system/power/delPermissionGroup', {id: id}).then(response => {
                this.$message.success(response.data.message)
                this.$emit('delete')
                this.loading = false
            }).catch(error => {
                this.$message.error(error.response.data.message)
                this.loading = false
            });
        },
    },
    data() {
        return {
            loading: false,
            cannotEdit: true,
            group: this.data
        }
    }
}



