import { Dialog, Input, Button, Form, FormItem, Card, Col, Row } from 'element-ui'
import GroupItem from './group_item'

export default {
    components: {
        'el-dialog': Dialog,
        'el-input': Input,
        'el-button': Button,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-card': Card,
        'el-col': Col,
        'el-row': Row,
        GroupItem
    },
    props: ['show'],
    data() {
        return {
            loading: {
                list: true,
                add: false
            },
            edit: true,
            dialogShow: false,
            formLabelWidth: '20px',
            form: {
                name: '',
            },
            permission_groups: []
        }
    },
    methods: {
        //
        add() {
            this.loading.add = true
            axios.post('api/system/power/addPermissionGroup', this.form).then(response => {
                this.getPermissionGroups()
                this.loading.add = false
            }).catch(error => {
                this.$message.error(error.response.data.message)
                this.loading.add = false
            });;
        },
        //
        getPermissionGroups() {
            axios.post('api/system/power/getPermissionGroups').then(response => {
                this.permission_groups = response.data.data;
                this.form.name = ''; //input
                this.form.action = ''; //input
                this.loading.list = false
            });
        }
    },
    watch: {
        show () {
            this.dialogShow = this.show;
        },
        dialogShow () {
            if (!this.dialogShow) {
                this.$emit('close');
            }
        },
        permission_groups(val) {
            this.permission_groups = val;
        }
    },
    mounted() {
        this.getPermissionGroups();
    }
}



