import { Dialog, Input, Button, Form, FormItem } from 'element-ui'

export default {
    components: {
        'el-dialog': Dialog,
        'el-input': Input,
        'el-button': Button,
        'el-form': Form,
        'el-form-item': FormItem,
    },
    props: ['show', 'role_id'],
    data() {
        return {
            loading: true,
            title: '',
            dialogShow: false,
            mode: 'add',
            formLabelWidth: '80px',
            form: {
                id: '',
                name: ''
            }
        }
    },
    methods: {
        addRole() {
            axios.post('api/system/power/addRole', {name: this.form.name}).then(response => {
                this.$message.success(response.data.message)
                this.dialogShow = false
            });
        },
        modifyRole() {
            axios.post('api/system/power/modifyRole', this.form).then(response => {
                this.$message.success(response.data.message)
                this.dialogShow = false
            })
        },
        getRole() {
            axios.post('api/system/power/getRole', {role_id: this.form.id}).then(response => {
                this.loading = false
                this.form = response.data.data
            })
        }
    },
    watch: {
        show () {
            this.dialogShow = this.show
        },
        dialogShow () {
            if (!this.dialogShow) {
                this.$emit('close')
                this.loading = true
                this.mode = 'add'
                this.form = {
                    id: '',
                        name: ''
                }
            } else {
                if (this.role_id) {
                    this.mode = 'edit'
                    this.title = ''
                    this.form.id = this.role_id
                    this.getRole()
                } else {
                    this.loading = false
                }
            }
        }
    }
}



