import { Dialog, Input, Button, Form, FormItem, Select, Option } from 'element-ui'

export default {
    components: {
        'el-dialog': Dialog,
        'el-input': Input,
        'el-button': Button,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-select': Select,
        'el-option': Option
    },
    props: ['show', 'data'],
    data() {
        return {
            loading: true,
            dialogShow: false,
            formLabelWidth: '10px',
            mode: 'add',
            form: {
                name: '',
                group_id: '',
                cn: {
                    name: ''
                }
            },
            permission_groups: []
        }
    },
    methods: {
        getPermissionGroups() {
            axios.post('api/system/power/getPermissionGroups').then(response => {
                this.permission_groups = response.data.data
                console.log(this.mode)
                if (this.mode == 'add') {
                    this.loading = false
                }
                if (this.permission_groups.length == 0) { //
                    this.$message.error('')
                    this.dialogShow = false
                }
            })
        },
        add() {
            axios.post('api/system/power/addPermission', this.form).then(response => {
                // this.form = {};
                this.$message.success(response.data.message)
                this.dialogShow = false
            }).catch(error => {
                this.$message.error(error.response.data.message)
            });
        },
        getGroupByPermissionID(permission_id) {
            axios.post('api/system/power/getGroupByPermissionID', {permission_id: permission_id}).then(response => {
                this.form.group_id = response.data.data
                this.loading = false
            })
        }
    },
    watch: {
        show () {
            this.dialogShow = this.show
        },
        dialogShow () {
            if (!this.dialogShow) {
                this.$emit('close')
                this.loading = true
                this.mode = 'add'
            } else {
                this.form = {
                    name: '',
                        group_id: '',
                        cn: {
                        name: ''
                    }
                },
                this.getPermissionGroups()
            }
        },
        data() {
            if (this.data) {
                this.form = this.data
                this.getGroupByPermissionID(this.form.id)
                this.mode = 'edit'
            }
        }
    }
}



